<template>
  <div>
    <p>Vuex Example</p>
    <div>
      Value: {{ count }}
      <button @click="increment">+</button>
      <button @click="decrement">-</button>
      <button @click="incrementIfOdd">Increment if odd</button>
      <button @click="incrementAsync">Increment async</button>
    </div>

    <p>Table Example</p>
    <el-table
      v-loading="loading"
      :data="movies"
      size="medium"
      border
      style="width: 100%">
      <el-table-column
        prop="id"
        label="编号"
        width="80px">
      </el-table-column>
      <el-table-column
        prop="title"
        label="名称"
        width="80px">
      </el-table-column>
      <el-table-column
        prop="directors"
        label="导演"
        width="180px"
        :formatter="formatDirectors">
      </el-table-column>
      <el-table-column
        label="操作">
        <template slot-scope="scope">
          <el-button
            size="mini"
            @click="handleAdd(scope.$index, scope.row)">添加</el-button>
        </template>
      </el-table-column>
    </el-table>
  </div>
</template>

<script>
import { mapGetters, mapActions } from 'vuex';
import axios from '../../lib/axios';
export default {
  data() {
    return {
      movies: [],
      loading: true
    }
  },

  //组件生命周期，组件已经创建，DOM 未生成
  created() {
    this.initData();
  },

  // 依赖的组件
  components: {
  },

  // 计算属性
  computed: mapGetters([
    'count',
    'recentHistory'
  ]),

  // 方法
  methods: {
    ...mapActions([
      'increment',
      'decrement',
      'incrementIfOdd',
      'incrementAsync'
    ]),

    formatDirectors(row, column, value) {
      return value.map(x => x.name).join(', ');
    },

    handleAdd() {
      this.$notify({
        title: 'It works!',
        type: 'success',
        message: 'but do nothing 🙈🙈',
        duration: 5000
      })
    },
    async initData() {
      axios({
        method: 'get',
        url: '/api/v2/movie/top250',
        headers: {'xy-proxy-host': 'example'}
      })
      .then(res => {
        const { subjects } = res.data || {};
        this.movies = subjects;
        this.loading = false;
      })
    },

  }
}
</script>


