import Example from '../page/example/index.vue';
import Home from '../page/home/index.vue';
import CinemaList from '../page/cinema/list.vue';


let routes = [
  {
    path: '/example',
    component: Example,
  },
  {
    path: '/',
    component: Home,
  },
  {
    path: '/cinema/list',
    component: CinemaList,
  },
  {
    path: '/home',
    redirect: '/',
  },
  {
    path: '*',
    redirect: '/',
  },
];

export default routes;


